*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  background-color: #050505;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.maintext {
  color: gold;
  margin-bottom: 150px;
}

.box {
  --width: 600px;
  --height: 600px;
  --depth: 600px;
  --hue: 30;
  --alpha: 0;

  width: var(--width);
  height: var(--height);
  position: relative;

  /* perspective: 600px; */
  transform-style: preserve-3d;

  /* animation: animateBox 5s infinite linear; */
  transform: rotateX(-10deg) rotateY(10deg);
  will-change: transform;
}

@keyframes animateBoxOne {
  0% {
    transform: rotateX(-10deg) rotateY(10deg);
  }
  100% {
    transform: rotateX(-10deg) rotateY(100deg);
  }
}
@keyframes animateBoxTwo {
  0% {
    transform: rotateX(-10deg) rotateY(100deg);
  }
  100% {
    transform: rotateX(-10deg) rotateY(190deg);
  }
}
@keyframes animateBoxThree {
  0% {
    transform: rotateX(-10deg) rotateY(190deg);
  }
  100% {
    transform: rotateX(-10deg) rotateY(280deg);
  }
}
@keyframes animateBoxFour {
  0% {
    transform: rotateX(-10deg) rotateY(280deg);
  }
  100% {
    transform: rotateX(-10deg) rotateY(370deg);
  }
}

.box__face {
  border: 1px solid white;
  position: absolute;
  background-color: hsla(var(--hue), 50%, 50%, var(--alpha));

  left: 50%;
  top: 50%;

  color: #fff;
  font-size: calc(1vw + 1rem);
  text-align: center;
  line-height: var(--height);
}

.box__face--back,
.box__face--front {
  width: 100%;
  height: 100%;

  --hue: 220;
  --alpha: 0;
  --coeff: -0.5;

  transform: translate(-50%, -50%)
    translate3d(0, 0, calc(var(--depth) * var(--coeff)));
}

.box__face--front {
  --coeff: 0.5;
}

.box__face--left,
.box__face--right {
  width: var(--depth);
  height: var(--height);

  --rotate: 90deg;
  --hue: 160;
  --alpha: 0;
  transform: translate(-50%, -50%) rotateY(var(--rotate))
    translate3d(0, 0, calc(var(--width) * 0.5));
}

.box__face--left {
  --rotate: -90deg;
}

.box__face--top,
.box__face--bottom {
  width: var(--width);
  height: var(--depth);
  --hue: 30;
  --alpha: 0;
  --rotate: 90deg;

  transform: translate(-50%, -50%) rotateX(var(--rotate))
    translate3d(0, 0, calc(var(--height) * 0.5));
}

.box__face--bottom {
  --rotate: -90deg;
}

.toleftone {
  transform: rotateX(-10deg) rotateY(100deg);
  animation: animateBoxOne 0.5s linear;
}

.tolefttwo {
  transform: rotateX(-10deg) rotateY(190deg);
  animation: animateBoxTwo 0.5s linear;
}

.toleftthree {
  transform: rotateX(-10deg) rotateY(280deg);
  animation: animateBoxThree 0.5s linear;
}

.toleftfour {
  transform: rotateX(-10deg) rotateY(370deg);
  animation: animateBoxFour 0.5s linear;
}
